  fetchChoice = (props) => {
    //Questions ID and choices:
    var qid = props.qid
    var choice = props.c
    console.log('QID '+qid)
    console.log('CHOICE '+choice)
    return this.state.questions[qid].choices[choice]
  }
  childCallback = (e) => {
    //This updates the choices for each question to generate the proper story:
    var qid = e.qid
    var new_choices = e.choices
    console.log('Got updated choices ' + new_choices + ' from my child: ' + qid)
    //Submit the question choices in the module state:
    var updated_questions = this.state.questions
    updated_questions[qid].choices = new_choices.slice()
    console.log('Updated choices: ', updated_questions[qid].choices)
    this.setState({questions: updated_questions}, () => {
      console.log('Updated the choices: '+updated_questions[qid].choices)
    })
  }
  generateRandomChoices = () => {
    console.log('Generating random choices for all the questions!');
    for (var i=0; i < this.state.ftQuestions.length; i++) {
      console.log('Calling out to child question ' + i)
      this.state.ftQuestions[i].generateAllChoices(i)
    }
  }
  renderStoryQuestions = () => {
    var story_questions
    if (this.state.storyType == 0) {
      story_questions = this.state.questions.map( (question, index) => 
      <FairytaleQuestion key={index} qid={index} q={question} cb={this.childCallback} onRef={ref => (this.state.ftQuestions[index] = ref)}/>
      )
    }else{
      story_questions = this.state.sf_questions.map( (question, index) => 
      <FairytaleQuestion key={index} qid={index} q={question} cb={this.childCallback} onRef={ref => (this.state.sfQuestions[index] = ref)}/>
      )
    }
    return story_questions
  }
