import React, { Component } from 'react';

class FairytaleQuestion extends Component {
    state = { 
        //Story fairytale:
        //questions: [],
        question: null,
        qid: 0,
        suggestion: '',
        submision: '',
        submitToParent: null,
    }
    constructor(props) {
        super(props);
        //Do it something!
        //this.setState({submision: suggestion})
        var qid = props.qid;
        var cb = props.cb;
        var q = props.q;
        console.log('Constructor got qid: ' + qid)
        this.state.qid = qid
        this.state.submitToParent = cb
        this.state.question = q
        console.log('Constructor got callback: ' + cb)
        //Change suggestion to userChoices and make it an array:
        //var userChoices = [];
        //for(var i = 0; i < choices.length; i++) {
        //    userChoices.push('');
        //}
        ////TODO:Does its work or needs split?
        //this.state.userChoices = userChoices;
    }
    componentDidMount() {
        this.props.onRef(this)
    }
    //Generate response for the choice id given:
    generateChoice = (cid) => {
        //Generate choice ONLY for specific choice id:
        var qid = this.state.qid
        var question_title = this.state.question.title
        var suggestions = this.state.question.suggestions
        console.log('Generating random choice for ('+cid+'): ' + question_title)
        //Randomly generate a choice
        var rnd_idx = Math.floor(Math.random() * suggestions.length) ;
        //Get random suggestion:
        var suggestion = suggestions[rnd_idx];
        //Update only cid user choice:
        var updated_question = this.state.question
        updated_question.choices[cid] = suggestion
        this.setState({question: updated_question});
        console.log(question_title + ' Suggested: ' + suggestion);
        //Submit to parent ALL the user choices:
        this.state.submitToParent(
            {
                //this changes from suggestion to userChoices
                choices: updated_question.choices,
                qid: qid
            }
        )
    }
    makeMultipleChoice = (cid) => {
        //Make/mark a multiple choice (choose ONE of multiple choices):
        console.log('Choice ID given for multiple choice: ' + cid)
        var qid = this.state.qid
        var question_title = this.state.question.title
        var suggestions = this.state.question.suggestions
        //Get instructed suggestion:
        var suggestion = suggestions[cid];
        //Update only cid user choice:
        var updated_question = this.state.question
        // I only have ONE choice to return!
        updated_question.choices[0] = suggestion
        this.setState({question: updated_question});
        console.log(question_title + ' Suggested: ' + suggestion);
        //Submit to parent ALL the user choices:
        this.state.submitToParent(
            {
                //this changes from suggestion to userChoices
                choices: updated_question.choices,
                qid: qid
            }
        )
    }
    generateAllChoices = (qid) =>{
        //Generate responses in all the user choices:
        console.log('Generating ALL responses ' + qid.toString())
        if (this.state.question.isMultipleChoice) {
            //TODO: you must chooce a random cid:
            this.makeMultipleChoice()
        }else{
            //pass
            for (var cid=0; cid<this.state.question.choices.length; cid++){
                this.generateChoice(cid)
            }
        }
    }
    makeFreeChoice = (props) => {
        console.log(props);
        var updatedValue = props.event.target.value;
        var cid = props.key;
        //this.setState({submision: updatedValue});
        var updated_question = this.state.question
        updated_question.choices[cid] = updatedValue
        this.setState({question: updated_question});
        //TODO: fix copy paste from above
        //var userChoices = this.state.userChoices;
        //console.log('Updating user choices '+userChoices.length+' from: '+userChoices)
        //userChoices[cid] = updatedValue
        //console.log('...to: '+userChoices)
        ////Update only cid user choice:
        //console.log(userChoices)
        ////TODO: works or needs split?
        //this.setState({submision: userChoices});
        //End TODO
        console.log('Submission done: ' + updatedValue);
        this.state.submitToParent(
            {
                choices: updated_question.choices,
                qid: this.state.qid
            }
        )
    }
    populateChoices = () => {
        //blah
        var choices_element
        //Determine if question is of multiple choice type:
        if (this.state.question.isMultipleChoice) {
            //Create a multiple choice element for the user:
            choices_element = (
            <div className="btn-group" role="group" aria-label="Basic example">
                {this.state.question.suggestions.map((option, index) => (
                    <button onClick={()=>this.makeMultipleChoice(index)} type="button" className="btn btn-secondary">{option}</button>
                ))}
            </div>
            )
        } else {
            //List all choice for the user to make:
            choices_element = this.state.question.choices.map((choice, index) => (
                <div className="btn-group-vertical">
                    <input value={this.state.question.choices[index]} onChange={(e)=>this.makeFreeChoice({event: e,key: index})} type="text" className="form-control" placeholder="Location" aria-label="Recipient's username" aria-describedby="button-addon2"/>
                    <button onClick={()=>this.generateChoice(index)} type="button" className="btn btn-outline-primary btn-long">Αυτόματη συμπλήρωση</button>
                </div>
            ))
        }
        return choices_element
    }
    render() { 
        return ( 
            <div className="col-sm">
                <div className="alert alert-light" role="alert">
                    <h5 className="alert-heading">{this.state.question.title}</h5>
                    <hr/>
                    {this.populateChoices()}
                </div>
            </div>
        );
    }
}
 
export default FairytaleQuestion;